@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin:wght@400;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
*,body,h1,h2,h3,div,section,p,ul,li{box-sizing:border-box;margin:0;padding:0}ul,li{list-style:none}a{color:inherit;text-decoration:none}address{font-style:normal}html{font-family:"Open Sans", sans-serif;font-size:11.6px;font-weight:300;scroll-behavior:smooth}body{width:100vw;overflow-x:hidden}h1,h2,h3,h4{font-family:"Kaisei Tokumin", serif}#container{width:84vw;max-width:1120px;margin:0 auto}.hover-button{background:linear-gradient(90deg, transparent calc(100% / 3), currentColor 0 calc(300% / 3), transparent 0) 100% 100%/200% 1px repeat-x;background:linear-gradient(90deg, transparent calc(100% / 3), currentColor 0 calc(300% / 3), transparent 0) var(--d, 100%) 100%/200% 1px repeat-x;cursor:pointer}.hover-button:hover{--d: -100%;transition:0.7s}.header{width:84vw;max-width:1120px;padding:30px 0 15px;display:flex;justify-content:space-between;position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:99}.header .header__title{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;align-items:center}.header .header__title .header__title__icon{width:18px;height:18px;background:black;border-radius:50%;margin-right:10px}.header .header__title__text,.header .header__location{font-family:"Open Sans", sans-serif;font-size:1rem;font-weight:400;line-height:1.2}.header .header__location{text-align:right}

